<template>
  <div class="comp deposit_detail">
    <div class="deposit_detail_info">
      <!--  -->
      <template v-if="payStatus == 0">
        <empty :tip-text="$t('UI_No_Channel')" />
      </template>
      <!--  -  -->
      <template v-if="payStatus == 1">
        <version-one ref="versionOneRef" />
      </template>
      <!--  -  -->
      <template v-if="payStatus == 2">
        <version-two ref="versionTwoRef" />
      </template>
      <!--  -  -->
      <template v-if="payStatus == 3">
        <version-three ref="versionThreeRef" />
      </template>
    </div>
    <!--  -->
    <div class="deposit_detail_btn">
      <div v-zoom class="btn btn_radius t_primary_dark_color" @click="onSubmit()">
        {{ $t('UI.Charge_2_upi_2') }}
      </div>
    </div>

    <!--  -->
    <kefu-inlet class="deposit_kefu_inlet" />
    <!-- cpf -->
    <custom-dialog v-model="cpfState">
      <div :style="{ width: isApp ? '100%' : '470' }">
        <cpf-bind @onClose="cpfState = false" />
      </div>
    </custom-dialog>
    <!--  -->
    <custom-dialog v-model="phoneState">
      <div :style="{ width: isApp ? '100%' : '470' }">
        <bind-info bind-data="phone" :title="$t('UI_recomendar_withdrawal_phoneTitle')" @back="phoneState = false" />
      </div>
    </custom-dialog>
    <!--  -->
    <deposit-info ref="depositInfoRef" />
    <!--  -->
    <deposit-qrcode ref="depositQrcodeRef" />
    <!--  -->
    <deposit-iframe ref="depositIframeRef" />
    <!-- SPEL -->
    <deposit-spel ref="depositSpelRef" />
  </div>
</template>

<script>
import { mapState } from 'vuex'
import KefuInlet from '@/components/ui/kefu-inlet'
import Empty from '@/components/ui/empty'
import VersionOne from './version-one'
import VersionTwo from './version-two'
import VersionThree from './version-three'
import CpfBind from '@/components/ui/cpf-bind'
import BindInfo from '@/components/ui/bind-info'
import DepositInfo from './ui/deposit-info.vue'
import DepositQrcode from './ui/deposit-qrcode.vue'
import DepositIframe from './ui/deposit-iframe.vue'
import DepositSpel from './ui/deposit-spel.vue'
import { cz } from '@/api/modules/config.js'
import { getEnvConfig } from '@/utils/collect.js'
import { lodash, CheckTGMinmiApp } from '@/utils/helper.js'
import { hasShowGuideInstall } from '@/utils/hasShowGuideInstall.js'
import { logEvent, ADS_TYPE, EVENT_KEY } from '@/server/ads/index.js'

export default {
  components: {
    Empty,
    KefuInlet,
    VersionOne,
    VersionTwo,
    VersionThree,
    CpfBind,
    BindInfo,
    DepositInfo,
    DepositQrcode,
    DepositIframe,
    DepositSpel
  },
  data() {
    return {
      // cpf 
      cpfState: false,
      // 
      phoneState: false,
      // 
      subParams: {},
      // 
      payAmount: 0,
      // 
      payChannelData: {},
      // 
      payChannelTypeItem: {},
      // 
      payActivity: false,
      // /
      payActivityInfo: {},
      // usdt
      payUsdtTotalAmount: '',
      // usdt
      payUsdt: false
    }
  },
  computed: {
    ...mapState('base', ['systemConfig', 'user', 'isApp', 'languageType']),
    /**
     * 
     * pay_channel_version 1() 2 3
     * recharge_show_version  01
     * @return 0: 1: 2: 3:
     */
    payStatus() {
      const { pay_channel_version = 1, recharge_show_version = 0 } = this.systemConfig

      // 
      if (pay_channel_version == 1 && recharge_show_version != 1) return 1
      // 
      if (pay_channel_version == 2) return 2
      // 
      if (pay_channel_version == 3 || (pay_channel_version == 1 && recharge_show_version == 1)) return 3

      return 0
    },
    /**
     * cpf
     * cpf_recharge_judge_switch 0 1
     * reg_bind_cpf cpf
     */
    isBindCpf() {
      const { cpf_recharge_judge_switch = 0 } = this.systemConfig
      const { reg_bind_cpf = '' } = this.user

      return cpf_recharge_judge_switch == 1 && !reg_bind_cpf
    },
    // 
    userTel() {
      const { tel = '' } = this.user

      return tel
    }
  },
  methods: {
    // 
    handleLog(data = {}) {
      // firebase/kwai 
      const filterTypes = [ADS_TYPE.firebase, ADS_TYPE.kwai]
      logEvent(EVENT_KEY.addToCart, { type: Object.values(ADS_TYPE).filter(i => !filterTypes.includes(i)) })
      logEvent(EVENT_KEY.addToCart, { type: filterTypes, params: data })
    },
    // cpf
    bindCpf() {
      this.$pop
        .confirm({
          content: this.$t('UI_CPF_TIP1'),
          confirmText: this.$t('UI.Charge_2_upi_2'),
          cancelText: this.$t('General.General_2'),
          success: () => {
            this.cpfState = true
          }
        })
        .show()
    },
    // 
    bindTel() {
      this.$pop
        .confirm({
          content: this.$t('UI_Bind_Phone_Text'),
          confirmText: this.$t('UI_Bind'),
          cancelText: this.$t('General.General_2'),
          success: () => {
            this.phoneState = true
          }
        })
        .show()
    },
    //  - 13450
    depositGiftLimit() {
      this.$pop
        .confirm({
          content: this.$t('UI_Deposit_Config_Text'),
          confirmText: this.$t('UI.Charge_2_upi_2'),
          cancelText: this.$t('General.General_2'),
          success: () => {
            this.$pop.confirm().hide(() => {
              this.payAction(true)
            })
          }
        })
        .show()
    },
    /**
     * 
     * @param isConfirm 
     * 9001 
     * 9002 
     */
    async payAction(isConfirm = false) {
      // 
      if (isConfirm) this.subParams.ordinary_confirm = 1
      // 
      this.handleLog({ currency: 'USD', value: this.payAmount })
      /**
       * 
       */
      const { return_type = 0, channel_id = '' } = this.payChannelData
      this.subParams = {
        ...this.subParams,
        token: this.user?.token,
        language: this.languageType,
        // 
        amount: this.payAmount,
        // id
        channel_id,
        // 
        return_type,
        //  0
        card_id: 0,
        //  1: 0:
        join_activity: this.payActivity ? 1 : 0,
        // tg
        is_tg_minmi: CheckTGMinmiApp() ? 1 : 0
      }
      // 
      if (return_type == 1) {
        const payUrl = `${getEnvConfig.baseURL()}/gold/cz?${this.$u.lodash.json2String(this.subParams, '&')}`
        window.open(payUrl, '_blank')
      }
      // pixUi -- 
      if (return_type == 2) {
        this.$pop.loading().show()
        const res = await cz(this.subParams).catch(e => e)
        this.$pop.loading().hide()
        // 
        if (!this.$u.lodash.isObject(res)) return
        // 
        if (res.code != 0) {
          if (res.code == 9001) {
            //  - 13450
            this.depositGiftLimit()
          } else {
            this.$pop.message().show({ content: res.msg || this.$t('UI_Deposit_Failure') })
          }
          return
        }

        // 
        const resData = res.data && this.$u.lodash.isObject(res.data) ? res.data : {}
        if (this.$refs['depositQrcodeRef']) {
          this.$refs['depositQrcodeRef'].open({ isUsdt: this.payUsdt, payInfo: resData })
        }
      }
      // 
      if (return_type == 3) {
        const payUrl = `${getEnvConfig.baseURL()}/gold/cz?${this.$u.lodash.json2String(this.subParams, '&')}`

        if (!this.isApp) return window.open(payUrl, '_blank')
        if (this.$refs['depositIframeRef']) this.$refs['depositIframeRef'].open({ iframeUrl: payUrl })
      }
      // SPEL
      if (return_type == 4) {
        this.$pop.loading().show()
        const res = await cz(this.subParams).catch(e => e)
        this.$pop.loading().hide()
        // 
        if (!this.$u.lodash.isObject(res)) return
        // 
        if (res.code != 0) {
          if (res.code == 9001) {
            //  - 13450
            this.depositGiftLimit()
          } else {
            this.$pop.message().show({ content: res.msg || this.$t('UI_Deposit_Failure') })
          }
          return
        }

        // SPEL
        const resData = res.data && this.$u.lodash.isObject(res.data) ? res.data : {}
        if (this.$refs['depositSpelRef']) this.$refs['depositSpelRef'].open({ isUsdt: this.payUsdt, payInfo: resData })
      }
    },
    // 
    onSubmit() {
      this.$nextTick(async () => {
        this.subParams = {}

        // 
        let minAmount = 0
        let maxAmount = 0

        /**
         *  -  
         */
        if (this.payStatus == 1 && this.$refs.versionOneRef) {
          const {
            amountInput = '',
            channelItem = {},
            movableSwitch = true,
            isUsdt = false,
            activityInfo = {},
            usdtTotalAmount = {},
            activityList = []
          } = this.$refs.versionOneRef.getPayData() || {}
          this.payAmount = parseFloat(String(amountInput).trim() || 0)
          this.payChannelData = channelItem
          this.payActivity = !!(movableSwitch && activityList.length)
          this.payActivityInfo = activityInfo
          this.payUsdtTotalAmount = usdtTotalAmount
          this.payUsdt = isUsdt

          // 
          const { min_amount = 0, max_amount = 0 } = this.payChannelData
          minAmount = isNaN(Number(min_amount)) ? 0 : parseFloat(min_amount)
          maxAmount = isNaN(Number(max_amount)) ? 0 : parseFloat(max_amount)
        }
        /**
         *  - 
         */
        if (this.payStatus == 2 && this.$refs.versionTwoRef) {
          const {
            amountInput = '',
            channelItem = {},
            movableSwitch = true,
            isUsdt = false,
            activityInfo = {},
            usdtTotalAmount = {},
            activityList = [],
            channelTypeItem = {}
          } = this.$refs.versionTwoRef.getPayData() || {}
          this.payAmount = parseFloat(String(amountInput).trim() || 0)
          this.payChannelData = channelItem
          this.payActivity = !!(movableSwitch && activityList.length)
          this.payActivityInfo = activityInfo
          this.payUsdtTotalAmount = usdtTotalAmount
          this.payUsdt = isUsdt
          this.payChannelTypeItem = channelTypeItem
          // id
          this.subParams.pay_type = this.payChannelTypeItem.type_id || ''

          // id
          if (!this.subParams.pay_type) return this.$pop.message().show({ content: this.$t('UI_Select_Payment') })
          // 
          const { amount_min = 0, amount_max = 0 } = this.payChannelTypeItem
          minAmount = isNaN(Number(amount_min)) ? 0 : parseFloat(amount_min)
          maxAmount = isNaN(Number(amount_max)) ? 0 : parseFloat(amount_max)
        }
        /**
         *  - 
         */
        if (this.payStatus == 3 && this.$refs.versionThreeRef) {
          const {
            amountInput = '',
            channelItem = {},
            movableSwitch = true,
            isUsdt = false,
            activityInfo = {},
            usdtTotalAmount = {},
            activityList = [],
            channelTypeItem = {}
          } = this.$refs.versionThreeRef.getPayData() || {}
          this.payAmount = parseFloat(String(amountInput).trim() || 0)
          this.payChannelData = channelItem
          this.payActivity = !!(movableSwitch && activityList.length)
          this.payActivityInfo = activityInfo
          this.payUsdtTotalAmount = usdtTotalAmount
          this.payUsdt = isUsdt
          this.payChannelTypeItem = channelTypeItem
          // id
          this.subParams.pay_type = this.payChannelTypeItem.type_id || ''

          // id
          if (!this.subParams.pay_type) return this.$pop.message().show({ content: this.$t('UI_Select_Payment') })
          // 
          const { min_amount = 0, max_amount = 0 } = this.payChannelData
          minAmount = isNaN(Number(min_amount)) ? 0 : parseFloat(min_amount)
          maxAmount = isNaN(Number(max_amount)) ? 0 : parseFloat(max_amount)
        }

        // 
        if (!lodash.isMoney(this.payAmount)) return this.$pop.message().show({ content: this.$t('UI.System_22') })
        // 
        if (this.payAmount > maxAmount) {
          return this.$pop.message().show({ content: `${this.$t('UI.System_26')} ${maxAmount}` })
        }
        if (this.payAmount < minAmount) {
          return this.$pop.message().show({ content: `${this.$t('UI.System_27')} ${minAmount}` })
        }

        // 
        if (hasShowGuideInstall(2)) return this.$pop.guideInstall().show()
        // /cpf
        if (this.isBindCpf) return this.bindCpf()
        /**
         * is_bind_tel  0: 1:
         * is_pop  0: 1:
         */
        const { is_bind_tel = 0, is_pop = 0 } = this.payChannelData
        // 
        if (is_bind_tel == 1 && !this.userTel) return this.bindTel()
        // 
        if (is_pop == 1 && this.$refs['depositInfoRef']) {
          const res = await this.$refs['depositInfoRef']
            .open({
              payAmount: this.payAmount,
              isUsdt: this.payUsdt,
              isActivity: this.payActivity,
              payActivityInfo: this.payActivityInfo,
              payUsdtTotalAmount: this.payUsdtTotalAmount,
              channelInfo: this.payChannelData
            })
            .catch(e => e)

          if (res == 'cancel') return
          this.subParams = Object.assign({}, this.subParams, res)
        }

        // 
        this.payAction()
      })
    }
  }
}
</script>

<style lang="scss" scoped>
