import { Decimal } from 'decimal.js-light'

/**
 * 
 * @param {*} x
 * @param {*} y
 * @param {*} symbol    
 */
export function calcFun(x = 0, y = 0, symbol = '+') {
  let xx = isNaN(Number(x)) ? 0 : Number(x)
  let yy = isNaN(Number(y)) ? 0 : Number(y)

  xx = new Decimal(xx)
  yy = new Decimal(yy)

  switch (symbol) {
    case '+':
      return xx.plus(yy).toNumber()
    case '-':
      return xx.sub(yy).toNumber()
    case '*':
      return xx.mul(yy).toNumber()
    case '/':
      return xx.div(yy).toNumber()
    default:
      return xx.plus(yy).toNumber()
  }
}
/**
 * 1 
 * @param {*} item 
 * @param {*} amount 
 */
export function calcRechargeGiftAmountV1(item = {}, amount = 0) {
  // activity_info 
  // up_limit_send_amount 
  // fixed_gold 
  // channel_recharge_activity_info 
  // send_percent 
  const newItem = JSON.parse(JSON.stringify(item))
  const { activity_info = {}, up_limit_send_amount = 0, fixed_gold = 0, channel_recharge_activity_info = [] } = newItem
  const { send_percent = 0 } = activity_info

  /**
   * 
   *  = ( * ) + 
   *  =  && ( > ) ?  : 
   * sendPercent: 
   * firstGiftMoney: 
   * activityAmount: 
   */
  const sendPercent = calcFun(parseFloat(send_percent), 100, '/')
  const firstGiftMoney = calcFun(sendPercent, parseFloat(amount), '*')
  let activityAmount = firstGiftMoney + parseFloat(fixed_gold)

  // 
  const maxAmount = parseFloat(up_limit_send_amount || 0)
  activityAmount = maxAmount && activityAmount > maxAmount ? maxAmount : activityAmount

  /**
   *  - 
   * channelPercent: 
   * channelRechargeAmount: 
   */
  let channelPercent = 0
  let channelRechargeAmount = 0
  const channelActivityList =
    channel_recharge_activity_info && Array.isArray(channel_recharge_activity_info)
      ? channel_recharge_activity_info
      : []
  if (channelActivityList.length) {
    channelPercent = channelActivityList.reduce((pre, cur) => {
      return calcFun(pre, parseFloat(cur.send_percent || 0), '+')
    }, 0)
    channelPercent = calcFun(channelPercent, 100, '/')
    channelRechargeAmount = calcFun(channelPercent, parseFloat(amount), '*')
  }

  return {
    // 
    no_limit_activity_amount: firstGiftMoney,
    // 
    activity_percent: sendPercent,
    // 
    activity_amount: activityAmount,
    // 
    channel_percent: channelPercent,
    // 
    channel_amount: channelRechargeAmount
  }
}
/**
 * 1 usdt
 * rate: USDT
 * gift_amount:  + 
 * amount: 
 */
export function calcUsdtTotalAmount(amount = 0, gift_amount = 0, rate = 0) {
  amount = parseFloat(amount || 0)
  const totalAmount = calcFun(calcFun(amount, gift_amount, '+'), rate, '*')

  return totalAmount
}
