/**
 * 
 * key 
 * weight 
 * type 
 * label 
 * placeholder 
 * value 
 * required 
 * isShow 
 * format:  cpf
 * rules 
 * rulesText 
 * disabled  false
 * radioList radio
 * radioTempData radio
 * selectList select
 * checkbox checkbox
 * msgText 
 */

/**
 * radioList -> radio
 * label 
 * value 
 * isShow 
 * weight 
 */

/**
 * selectList -> select
 * label 
 * value 
 */

import store from '@/store'

// 
const TEMP_TYPE = {
  INPUT: 'input',
  RADIO: 'radio',
  CHECKBOX: 'checkbox',
  SELECT: 'select'
}

// 
const rules = {
  // 
  notEmpty: (val = '') => {
    const value = val.trim()
    return !!value
  },
  // cpf
  cpfCheck: (val = '') => {
    const value = val.trim()
    return /^\d{11}(?:\d{3})?$/.test(value)
  },
  // 
  pakPhoneCheck: (val = '') => {
    const value = val.trim()
    return /^\d{11}$/.test(value)
  }
}

/**
 * 
 * auto_fill_status  0 1 --> 
 * auto_fill_unchange_status  0 1 --> 
 * cpf_tips - 
 * recharge_page_mobile_desc 
 */
export const brazilTemp = (payConfig = {}) => {
  const { user = {}, systemConfig = {} } = store?.state?.base || {}
  const { pay_userinfo_fields = {}, recharge_page_mobile_desc = '' } = systemConfig
  const { pay = {}, cpf_tips = '' } = pay_userinfo_fields || {}
  const { recharge_fields = {} } = user
  const { auto_fill_status = 0, auto_fill_unchange_status = 0 } = payConfig

  const temp = [
    // 
    {
      key: 'name',
      weight: 'name',
      type: TEMP_TYPE.INPUT,
      label: 'UI_Deposit_UserName',
      placeholder: 'UI.Charge_1_info_3',
      value: auto_fill_status == 1 ? recharge_fields.name || '' : '',
      required: true,
      disabled: auto_fill_unchange_status == 1 && auto_fill_status == 1 && recharge_fields.name ? true : false,
      isShow: pay['name'] == 1,
      rules: rules.notEmpty,
      rulesText: 'UI.Charge_1_info_3'
    },
    // 
    {
      key: 'phone',
      weight: 'phone',
      type: TEMP_TYPE.INPUT,
      label: 'UI_Deposit_PhoneNumber',
      placeholder: 'UI.Charge_1_info_4',
      value: auto_fill_status == 1 ? recharge_fields.phone || '' : '',
      required: true,
      disabled: auto_fill_unchange_status == 1 && auto_fill_status == 1 && recharge_fields.phone ? true : false,
      isShow: pay['phone'] == 1,
      rules: rules.notEmpty,
      rulesText: 'UI.Charge_1_info_4',
      msgText: recharge_page_mobile_desc.replaceAll('\n', '<br/>')
    },
    // 
    {
      key: 'email',
      weight: 'email',
      type: TEMP_TYPE.INPUT,
      label: 'UI.cashout_1_bank_12',
      placeholder: 'UI_Deposit_emailPlaceholder',
      value: auto_fill_status == 1 ? recharge_fields.email || '' : '',
      required: true,
      disabled: auto_fill_unchange_status == 1 && auto_fill_status == 1 && recharge_fields.email ? true : false,
      isShow: pay['email'] == 1,
      rules: rules.notEmpty,
      rulesText: 'UI_Deposit_emailPlaceholder'
    },
    // -
    {
      key: 'taxid',
      weight: 'taxid',
      type: TEMP_TYPE.INPUT,
      label: 'UI_DepositId',
      placeholder: 'UI_Shop_InputTaxId',
      value: auto_fill_status == 1 ? recharge_fields.taxid || '' : '',
      required: true,
      isShow: pay['taxid'] == 1,
      format: 'cpf',
      rules: rules.cpfCheck,
      rulesText: 'UI_Shop_InputTaxId',
      msgText: cpf_tips.replaceAll('\n', '<br/>')
    },
    // -
    {
      key: 'zipcode',
      weight: 'zipcode',
      type: TEMP_TYPE.INPUT,
      label: 'UI_DepositIdCode',
      placeholder: 'UI_Shop_InputZipCode',
      value: auto_fill_status == 1 ? recharge_fields.zipcode || '' : '',
      required: true,
      isShow: pay['zipcode'] == 1,
      rules: rules.notEmpty,
      rulesText: 'UI_Shop_InputZipCode'
    }
  ]
  const list = temp.filter(item => item.isShow)

  return list
}

/**
 * 
 * auto_fill_status  0 1 --> 
 * auto_fill_unchange_status  0 1 --> 
 * recharge_page_mobile_desc 
 */
export const pakistanTemp = (payConfig = {}) => {
  const { user = {}, systemConfig = {} } = store?.state?.base || {}
  const { pay_userinfo_fields = {}, recharge_page_mobile_desc = '' } = systemConfig
  const { pay = {} } = pay_userinfo_fields || {}
  const { recharge_fields = {} } = user
  const { auto_fill_status = 0, auto_fill_unchange_status = 0 } = payConfig

  const temp = [
    // 
    {
      key: 'name',
      weight: 'name',
      type: TEMP_TYPE.INPUT,
      label: 'UI_Deposit_UserName',
      placeholder: 'UI.Charge_1_info_3',
      value: auto_fill_status == 1 ? recharge_fields.name || '' : '',
      required: true,
      disabled: auto_fill_unchange_status == 1 && auto_fill_status == 1 && recharge_fields.name ? true : false,
      isShow: pay['name'] == 1,
      rules: rules.notEmpty,
      rulesText: 'UI.Charge_1_info_3'
    },
    // 
    {
      key: 'phone',
      weight: 'phone',
      type: TEMP_TYPE.INPUT,
      label: 'UI_Deposit_PhoneNumber',
      placeholder: 'UI.Charge_1_info_4',
      value: auto_fill_status == 1 ? recharge_fields.phone || '' : '',
      required: true,
      disabled: auto_fill_unchange_status == 1 && auto_fill_status == 1 && recharge_fields.phone ? true : false,
      isShow: pay['phone'] == 1,
      rules: rules.notEmpty,
      rulesText: 'UI.Charge_1_info_4',
      msgText: recharge_page_mobile_desc.replaceAll('\n', '<br/>')
    },
    // 
    {
      key: 'email',
      weight: 'email',
      type: TEMP_TYPE.INPUT,
      label: 'UI.cashout_1_bank_12',
      placeholder: 'UI_Deposit_emailPlaceholder',
      value: auto_fill_status == 1 ? recharge_fields.email || '' : '',
      required: true,
      disabled: auto_fill_unchange_status == 1 && auto_fill_status == 1 && recharge_fields.email ? true : false,
      isShow: pay['email'] == 1,
      rules: rules.notEmpty,
      rulesText: 'UI_Deposit_emailPlaceholder'
    },
    //  -- JazzCash/EasyPaise
    {
      key: 'pk_type',
      weight: 'pk_type',
      type: TEMP_TYPE.RADIO,
      label: 'UI_Deposit_Field_Jazz_Easy_Label',
      placeholder: '',
      value: user['pk_type'] || '',
      required: true,
      isShow: pay['pk_jazzcash'] == 1 || pay['pk_easypaise'] == 1,
      radioList: [
        {
          label: 'UI.Withdraw_Pk_Prepay_Type_Pk_Jazz_Cash',
          value: 'JazzCash',
          isShow: pay['pk_jazzcash'] == 1,
          weight: 'pk_jazzcash'
        },
        {
          label: 'UI.Withdraw_Pk_Prepay_Type_Pk_Easy_Paise',
          value: 'EasyPaise',
          isShow: pay['pk_easypaise'] == 1,
          weight: 'pk_easypaise'
        }
      ],
      rules: rules.notEmpty,
      rulesText: 'UI_Deposit_Field_Jazz_Easy_Rule'
    }
  ]

  const list = temp
    .filter(item => item.isShow)
    .map(item => {
      const { radioList, ...rest } = item
      let newItem = { ...rest }
      if (radioList) newItem.radioList = radioList.filter(f => f.isShow)

      return newItem
    })

  return list
}

